


















import {
	mapState,mapMutations
} from 'vuex'
var _self, x, y;
export default {
	data() {
		return {
			//2个按钮的宽度
			btn1Width: 70,
			btn2Width: 70,
			//2个按钮的总宽 = btn1Width + btn23Width【uni-app 模板内不建议复杂运算请手动配置】
			deleteBtnWidth: 140,
			//正在滑动的索引
			scrollIndex: -1,
			//正在删除的索引
			deleteIndex: -1,
		}
	},
	computed: mapState(['msgList']),
	onLoad: function() {
		_self = this;
	},
	onPullDownRefresh:function (){
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		...mapMutations(['changeMsg','delMsg']),
		goChat: function(to_id,index) {
			this.changeMsg(index);
			uni.navigateTo({
				url: './chat?to_id=' + to_id
			});
		},
		removeMsg: function(e) {
			var index = e.target.dataset.id;
			//删除确认
			if (this.deleteIndex != index) {
				this.deleteIndex = index;
				setTimeout(function() {
					_self.deleteIndex = -1;
				}, 2000);
				return false;
			}
			this.scrollIndex = -1;
			setTimeout(function() {
				_self.delMsg(index);
				_self.deleteIndex = -1;
			}, 500);
		},
		touchStart: function(e) {
			x = e.mp.changedTouches[0].clientX;
			y = e.mp.changedTouches[0].clientY;
		},
		touchEnd: function(e) {
			x = x - e.mp.changedTouches[0].clientX;
			y = y - e.mp.changedTouches[0].clientY;
			if (Math.abs(x) < 50) {
				return;
			}
			if (Math.abs(x) < Math.abs(y)) {
				return;
			}
			var index = e.currentTarget.dataset.id;
			if (index == this.scrollIndex) {
				return;
			}
			_self.scrollIndex = index;
		},
		changStatus: function(e) {
			var index = e.currentTarget.dataset.id;
			this.changeMsg(index);
		}
	}
}
